.footer {
  height: $footerHeightMobile;
  background-image: url("/images/footer_320.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;

  @include tablet {
    height: $footerHeightTablet;
    background-image: url("/images/footer_744.png");
  }

  @include desktop {
    height: $footerHeightDesktop;
    background-image: url("/images/footer_1260.png");
  }
}
